import React, { Component } from "react";
import {
  Col,
  Row,
  Form,
  Input,
  Button,
  InputNumber,
  Radio,
  Upload,
  Icon
} from "antd";

const FormItem = Form.Item;
const { TextArea } = Input;

class NewPlace extends Component {
  constructor() {
    super();
    this.state = {
      form: {
        name: "",
        price: "",
        description: "",
        type: "",
        size: "",
        photos: []
      }
    };
  }

  handleChangeNumber = e => {
    let { photos } = this.state.form;

    photos = e.fileList;

    photos = photos.slice(-2);

    photos = photos.map(file => {
      if (file.response) {
        file.url = file.response.url;
      }
      return file;
    });

    photos = photos.filter(file => {
      if (file.response) {
        return file.response.status === "success";
      }
      return false;
    });

    this.setState({ photos });
  };

  handleUploadPhotos = e => {
    let { form } = this.state;
    console.log(form);
    let fileList = e.fileList.name;
    console.log(fileList);

    this.setState({ form });
  };

  handleChange = e => {
    let { form } = this.state;

    let field = e.target.name;
    form[field] = e.target.value;

    this.setState({ form });
  };

  render() {
    return (
      <div className="container">
        <Row>
          <Col span={8} offset={8}>
            <Form onSubmit={this.handleSubmit} className="login-form">
              <FormItem>
                <Input
                  placeholder="Nombre"
                  name="name"
                  onChange={this.handleChange}
                />
              </FormItem>
              <FormItem>
                <InputNumber
                  name="price"
                  min={1}
                  formatter={value =>
                    `$ ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                  }
                  parser={value => value.replace(/\$\s?|(,*)/g, "")}
                  onChange={this.handleChangeNumber}
                />{" "}
                Pesos
              </FormItem>
              {/*<FormItem>
                <Input
                  placeholder="Direccion"
                  name="address"
                  onChange={this.handleChange}
                />
              </FormItem>*/}
              <FormItem>
                <TextArea
                  name="description"
                  rows={4}
                  placeholder="Describe tu espació"
                  onChange={this.handleChange}
                />
              </FormItem>
              <FormItem>
                <Radio.Group
                  defaultValue="STORAGE"
                  name="type"
                  buttonStyle="outline"
                  size="small"
                  onChange={this.handleChange}
                >
                  <Radio.Button value="STORAGE">Almacenamiento</Radio.Button>
                  <Radio.Button value="ACOOMMODATION">Alojamiento</Radio.Button>
                </Radio.Group>
              </FormItem>
              <FormItem>
                <Radio.Group
                  defaultValue="MEDIUM"
                  name="size"
                  buttonStyle="outline"
                  size="small"
                  onChange={this.handleChange}
                >
                  <Radio.Button value="SMALL">Pequeño</Radio.Button>
                  <Radio.Button value="MEDIUM">Mediano</Radio.Button>
                  <Radio.Button value="BIG">Grande</Radio.Button>
                </Radio.Group>
              </FormItem>
              <FormItem>
                <Upload
                  name="photos"
                  onChange={this.handleUploadPhotos}
                  listType="picture"
                  multiple
                >
                  <Button>
                    <Icon type="upload" /> Upload
                  </Button>
                </Upload>
              </FormItem>
              <FormItem>
                <Button
                  type="primary"
                  htmlType="submit"
                  className="login-form-button"
                >
                  Crear Espacio
                </Button>
              </FormItem>
            </Form>
          </Col>
        </Row>
      </div>
    );
  }
}

export default NewPlace;
